.customPagination {
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: $secondary-text-color;
  border-color: $secondary-text-color;
  border-radius: 5px;
  cursor: default;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.tui-pagination .tui-is-selected:hover {
  background-color: #4b96e6;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first-child,
.tui-pagination .tui-prev-is-ellip {
  border-left: 1px solid #ddd;
}

.tui-pagination .tui-last-child,
.tui-pagination .tui-next-is-ellip {
  border-right: 1px solid #ddd;
}

.tui-pagination .tui-first-child.tui-is-selected {
  border-left: 1px solid #4b96e6;
}

.tui-pagination .tui-last-child.tui-is-selected {
  border-right: 1px solid #4b96e6;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  background-color: $footer-bg-color;
  overflow: hidden;
  height: 40px;
  width: 40px;
  padding: 0;

  white-space: nowrap;
  font-weight: normal;
  border: none;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
}

.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}

.tui-pagination .tui-next-is-ellip {
  border-left: 0;
}

.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  border-left: 0;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  background: none;
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-prev {
  margin-right: 10px;
}

.tui-pagination .tui-next {
  margin-left: 10px;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: default;
  filter: none;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
  filter: none;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
  filter: none;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
  filter: none;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: #4b96e6;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}
.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 40px;
  padding: 14px 0 6px;
  margin-left: 2px;
  margin-right: 2px;

  font-size: 12px;

  text-decoration: none;
  vertical-align: middle;
  border: none;

  height: 40px;
  font-weight: 500;
  line-height: 1.16;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.tui-pagination {
  height: 40px;
  margin: 0 0 60px;
  line-height: normal;
  text-align: center;
  font-size: 0;

  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.tui-pagination .tui-first {
  display: none;
}
.tui-pagination .tui-last {
  display: none;
}

